.reviews {
  padding-top: 60px;
  @include desktop {
    padding-top: 120px;
  }
}
.reviews__list {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.reviews__img {
  width: 85px;
  height: 85px;
  margin-bottom: 32px;
}

.reviews__text {
  background-color: var(--review-background-color);
  width: 280px;
  height: 308px;
  margin-bottom: 36px;

  @include tablet {
    width: 704px;
    height: 164px;
    margin-bottom: 24px;
  }

  @include desktop {
    width: 968px;
    height: 138px;
    margin-bottom: 36px;
  }
}

.linear-gradient {
  height: 96%;
  margin: 0 auto;
  z-index: 1;

  width: 180px;
  background-image: repeating-linear-gradient(
    to bottom,
    var(--review-background-color),
    var(--review-background-color) 74px,
    var(--linear-gradient-color) 74px,
    var(--linear-gradient-color) 75px,
    var(--review-background-color) 75px,
    var(--review-background-color) 105px,
    var(--linear-gradient-color) 106px,
    var(--linear-gradient-color) 107px,
    var(--review-background-color) 107px,
    var(--review-background-color) 136px,
    var(--linear-gradient-color) 136px,
    var(--linear-gradient-color) 137px,
    var(--review-background-color) 137px,
    var(--review-background-color) 167px,
    var(--linear-gradient-color) 167px,
    var(--linear-gradient-color) 168px,
    var(--review-background-color) 168px,
    var(--review-background-color) 198px,
    var(--linear-gradient-color) 198px,
    var(--linear-gradient-color) 199px,
    var(--review-background-color) 199px,
    var(--review-background-color) 229px,
    var(--linear-gradient-color) 230px,
    var(--linear-gradient-color) 231px,
    var(--review-background-color) 231px,
    var(--review-background-color) 260px,
    var(--linear-gradient-color) 260px,
    var(--linear-gradient-color) 261px,
    var(--review-background-color) 261px,
    var(--review-background-color) 291px,
    var(--linear-gradient-color) 292px,
    var(--linear-gradient-color) 293px
  );

  @include tablet {
    width: 608px;
    background-image: repeating-linear-gradient(
      to bottom,
      var(--review-background-color),
      var(--review-background-color) 68px,
      var(--linear-gradient-color) 68px,
      var(--linear-gradient-color) 69px,
      var(--review-background-color) 69px,
      var(--review-background-color) 99px,
      var(--linear-gradient-color) 99px,
      var(--linear-gradient-color) 100px,
      var(--review-background-color) 100px,
      var(--review-background-color) 130px,
      var(--linear-gradient-color) 130px,
      var(--linear-gradient-color) 131px
    );
  }

  @include desktop {
    width: 822px;
    background-image: repeating-linear-gradient(
      to bottom,
      var(--review-background-color),
      var(--review-background-color) 76px,
      var(--linear-gradient-color) 76px,
      var(--linear-gradient-color) 77px,
      var(--review-background-color) 77px,
      var(--review-background-color) 104px,
      var(--linear-gradient-color) 104px,
      var(--linear-gradient-color) 105px
    );
  }
}

.customer__text {
  position: relative;
  display: block;
  width: 157px;
  padding-top: 43px;
  margin: 0 auto;
  text-align: center;
  color: var(--black-text-color);
  z-index: 1;

  @include font(500, 12px, 2.55, 0.04em);

  &::before {
    position: absolute;
    content: '';
    top: 30px;
    left: -15px;
    width: 54px;
    height: 40px;
    background-image: url('../images/quotation.png');
    z-index: -1;

    @include tablet {
      width: 60px;
      top: 20px;
      left: -45px;
      background-size: cover;
    }

    @include desktop {
      width: 54px;
      top: 30px;
      left: -35px;
    }
  }

  @include tablet {
    @include font(500, 14px, 2.16, 0.04em);
    padding-top: 37px;
    width: 515px;
  }

  @include desktop {
    @include font(500, 16px, 1.86, 0.04em);
    padding-top: 47px;
    width: 750px;
  }
}

.customer__name {
  position: relative;
  display: flex;
  justify-content: center;
  width: 172px;
  color: var(--black-text-color);

  @include font(500, 14px, 1.86, 0.04em);

  &::before {
    position: absolute;
    top: -16px;
    justify-content: center;
    content: '';
    width: 32px;
    height: 4px;
    background-image: url('../images/ellipsis.png');
  }

  @include tablet {
    @include font(500, 16px, 1.86, 0.04em);
    width: 515px;
  }

  @include desktop {
    @include font(500, 20px, 1.86, 0.04em);
    width: 750px;
  }
}

.my-dots {
  position: absolute;
  bottom: -28px;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  text-align: center;

  @include mobile {
    max-width: 440px;
  }

  @include tablet {
    max-width: 704px;
    bottom: -44px;
  }

  @include desktop {
    max-width: 1168px;
  }
}

.my-dots li {
  position: relative;
  display: inline-block;
  width: 12px;
  height: 12px;
  margin: 0 6px;
  padding: 0;
  cursor: pointer;
}

.my-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 12px;
  height: 12px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.my-dots li button:hover,
.my-dots li button:focus {
  outline: none;
}

.my-dots li button:hover:before,
.my-dots li button:focus:before {
  opacity: 1;
}

.my-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 12px;
  position: absolute;
  top: 0;
  left: 0;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  content: '';
  text-align: center;
  background-color: #d9d9d9;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.my-dots li.slick-active button:before {
  content: url('../images/home.png');
  width: 100%;
  height: 100%;

  background-color: transparent;
}
